/**
 * 
 * @param {string} args   
 * @return {object} state:   msg: 
 * */
export function validatePhone(args: string): { state: boolean; msg: string } {
  if (!args || args.length === 0) {
    return {
      state: false,
      msg: "",
    };
  }

  if (!/^1(3|4|5|6|7|8|9)\d{9}$/.test(args)) {
    return {
      state: false,
      msg: "",
    };
  }

  return { state: true, msg: "" };
}

